html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1.5;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

.header {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  position: relative;
}

.header__particles {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 1;
}

.header__wrapper {
  display: flex;
  flex-direction: column;
}

.header__nav-wrapper {
  width: 100%;
  max-width: 1000px;
  height: 80px;
}

.header__logo-link {
  display: flex;
}

.header__logo-image {
  width: 100%;
  height: 100%;
  max-width: 60px;
  z-index: 2;
}

.header__nav {
  width: 100%;
  max-width: 1000px;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

.header__nav-desktop {
  display: flex;
}

.header__nav-desktop__item {
  list-style: none;
  margin-right: 10px;
  z-index: 2;
}

.header__nav-desktop__item:last-child {
  margin-right: 0;
}

.header__nav-desktop__link {
  display: inline-block;
  color: #ffffff;
  text-decoration: none;
  padding: 10px 30px;
  text-transform: capitalize;
  transition: 0.7s;
  font-size: 1rem;
  font-weight: 400;
}

.header__nav-desktop__link:hover {
  background: rgba(255, 255, 255, 0.1);
}

.header__nav-button {
  width: 30px;
  height: 30px;
  display: none;
  z-index: 5;
}

.header__nav-button:hover {
  cursor: pointer;
}

.header__nav-button-line {
  position: relative;
  width: 30px;
  height: 5px;
  background-color: #fff;
}

.header__nav-button-line:before {
  content: '';
  position: absolute;
  top: -10px;
  left: 0;
  display: block;
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.header__nav-button-line:after {
  content: '';
  position: absolute;
  top: 10px;
  left: 0;
  display: block;
  width: 30px;
  height: 5px;
  background-color: #fff;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.button-active .header__nav-button-line {
  background-color: transparent;
}

.button-active .header__nav-button-line:hover {
  cursor: pointer;
}

.button-active .header__nav-button-line:before {
  top: 0;
  transform: rotate(45deg);
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.button-active .header__nav-button-line:after {
  top: 0;
  transform: rotate(-45deg);
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.header__nav-mobile-container {
  display: none;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  padding: 80px 50px;
  background-color: #000000;
  z-index: 4;
}

.header__nav-mobile {
  width: 100%;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: auto;
}

.header__nav-mobile__item {
  margin-bottom: 20px;
}

.header__nav-mobile__item:last-child {
  margin-bottom: 0;
}

.header__nav-mobile__link {
  display: block;
  text-align: center;
  color: #ffffff;
  text-decoration: none;
  text-transform: capitalize;
  transition: 0.7s;
  font-size: 1rem;
  font-weight: 400;
  padding: 30px 20px;
  border: 1px solid #191919;
}

.header__nav-mobile__link:hover {
  background: rgba(255, 255, 255, 0.1);
}

.header__nav .container-disabled {
  display: none;
}

.header__fixed {
  position: fixed;
  top: 0;
  z-index: 3;
  background-color: #000000;
  border-bottom: 1px solid #aaaaaa;
  transition: background-color 0.5s linear, border-bottom 0.1s linear 0.5s, position 0.5s ease-in 0.6s;
}

.header__content {
  width: 100%;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
}

.header__content-title, .header__content-subtitle, .header__content-about {
  text-transform: uppercase;
}

.header__content-title {
  font-size: 5.5rem;
  color: #ffffff;
  text-align: center;
  font-weight: 100;
}

.header__content-subtitle {
  width: 100%;
  max-width: 400px;
  text-align: center;
  color: #eeeeee;
  font-weight: 700;
}

.header__content-about-content {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5% 5% 0 5%;
}

.header__content-image {
  width: 10%;
  min-width: 200px;
  border: 1px solid #ffffff;
}

.header__content-about {
  width: 100%;
  height: 100%;
  max-width: 400px;
  text-align: left;
  color: #eeeeee;
  font-size: 0.8rem;
  font-weight: 400;
  padding: 40px 0 10px 20px;
}

.header__content-about-sections {
  margin-bottom: 10px;
}

.header__scroll {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto 5% auto;
  display: inline-block;
  position: relative;
  padding: 0 20px;
  z-index: 2;
}

.header__scroll-link {
  display: inline-block;
  color: #bbbbbb;
  text-decoration: none;
  text-transform: uppercase;
  padding: 10px 20px;
  background: #000000;
  font-weight: 400;
  transition: 0.7s;
}

.header__scroll-link:hover {
  color: #ffffff;
}

.header__scroll:before {
  content: '';
  width: 100%;
  height: 2px;
  position: absolute;
  top: calc(50% - 2px);
  left: 0;
  background: #eeeeee;
  z-index: -1;
}

@media (max-width: 767px) {
  .header__content-title {
    font-size: 3.5rem;
  }
}

@media (max-width: 575px) {
  .header__nav-desktop {
    display: none;
  }
  .header__nav-button {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header__nav-mobile-container {
    display: flex;
  }
  .header__content-title {
    font-size: 3rem;
  }
  .header__content-subtitle {
    max-width: 300px;
    font-size: 0.8rem;
  }
  .header__content-about-content {
    flex-direction: column;
  }
  .header__content-image {
    min-width: 200px;
  }
  .header__content-about {
    font-size: 0.8rem;
    padding: 20px 0 0 0;
    text-align: center;
    max-width: 300px;
  }
  .header__scroll {
    margin: 0 auto 20% auto;
  }
}

.header:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #000000;
  z-index: -1;
}

.my-work {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 60px 50px;
  width: 100%;
  background: #000000;
}

.my-work__wrapper {
  width: 100%;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  padding: 30px 20px;
}

.my-work__title {
  font-size: 2rem;
  font-weight: 700;
  color: #ffffff;
  text-transform: uppercase;
}

.my-work__subtitle {
  color: #dddddd;
  font-weight: 300;
  font-size: 0.9rem;
  text-transform: uppercase;
  padding: 20px 0 60px 0;
}

.my-work__subtitle .subtitle {
  margin-bottom: 20px;
}

.my-work__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.my-work__list-block {
  margin: auto;
  width: 100%;
  max-width: 350px;
  padding: 20px 15px;
}

.my-work__list-lists {
  color: #dddddd;
  font-size: 0.9rem;
  text-transform: uppercase;
  font-weight: 300;
  list-style-type: disc;
}

.my-work__list-title {
  font-size: 1.3rem;
  font-weight: 700;
  color: #ffffff;
  text-transform: capitalize;
}

.my-work__list-link {
  color: #ffffff;
  text-decoration: none;
  transition: 0.7s;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.my-work__list-link:hover {
  background: rgba(255, 255, 255, 0.3);
}

@media (max-width: 767px) {
  .my-work__list {
    flex-direction: column;
  }
}

@media (max-width: 575px) {
  .my-work {
    padding: 70px 0;
  }
  .my-work__title {
    text-align: center;
  }
  .my-work__subtitle {
    font-size: 0.8rem;
    text-align: center;
  }
}

.projects {
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 60px 50px;
  background: #000000;
}

.projects__wrapper {
  width: 100%;
  height: 100%;
  max-width: 1000px;
  display: flex;
  flex-direction: column;
  padding: 30px 20px;
}

.projects__title {
  text-align: right;
  font-weight: 700;
  font-size: 2rem;
  color: #ffffff;
  text-transform: uppercase;
}

.projects__subtitle {
  width: 100%;
  max-width: 700px;
  text-align: right;
  align-self: flex-end;
  color: #dddddd;
  font-size: 0.9rem;
  text-transform: uppercase;
  font-weight: 300;
  padding: 20px 0 60px 0;
}

.projects__cards {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.projects__card {
  height: 300px;
  width: 100%;
  max-width: 300px;
  perspective: 1000px;
  margin: 10px;
  position: relative;
}

.projects__card-front, .projects__card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  backface-visibility: hidden;
  border-radius: 10px;
  color: #ffffff;
  transition: 0.7s;
}

.projects__card-front {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #000000;
  padding: 15px 10px;
  border: 1px solid #aaaaaa;
}

.projects__card-front-image {
  width: 70%;
  height: 70%;
  border-radius: 10px;
  object-fit: contain;
}

.projects__card-back {
  transform: rotateY(-180deg);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background: #191919;
  padding: 20px 10px;
}

.projects__card-back-repo-link {
  display: block;
  color: #ffffff;
  text-decoration: none;
  transition: 0.7s;
  font-size: 1.5rem;
}

.projects__card-back-desc {
  width: 90%;
  text-indent: 3%;
  color: #dddddd;
  font-size: 0.8rem;
  font-weight: 400;
  text-align: center;
  text-transform: uppercase;
}

.projects__card-back-link {
  color: #ffffff;
  text-decoration: none;
  text-transform: capitalize;
  padding: 10px 15px;
  transition: 0.7s;
}

.projects__card-back-link:hover {
  cursor: pointer;
  background: rgba(255, 255, 255, 0.1);
}

.projects__card-back-repo-link:hover {
  background: rgba(255, 255, 255, 0.1);
}

.projects__card:hover .projects__card-front {
  transform: rotateY(-180deg);
}

.projects__card:hover .projects__card-back {
  transform: rotateY(-360deg);
}

@media (max-width: 767px) {
  .projects__cards {
    flex-direction: column;
  }
}

@media (max-width: 575px) {
  .projects {
    padding: 70px 0;
  }
  .projects__title {
    text-align: center;
  }
  .projects__subtitle {
    text-align: center;
    align-self: center;
    font-size: 0.8rem;
  }
}

.footer {
  position: relative;
  padding: 200px 50px;
  background: #000000;
}

.footer__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  border-radius: 50% 50% 0 0 / 100% 100% 0 0;
  background: #191919;
}

.footer__logo-wrapper {
  display: flex;
}

.footer__logo {
  width: 100%;
  height: 100%;
  max-width: 60px;
  z-index: 2;
}

.footer__social-block {
  max-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.footer__social-block-links {
  list-style: none;
  margin: 0 10px 10px 10px;
}

.footer__social-block-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  color: #ffffff;
  font-size: 1.5rem;
  text-decoration: none;
  background: #000000;
  border-radius: 50%;
  transition: 0.7s;
}

.footer__social-block-link:hover {
  background: rgba(255, 255, 255, 0.1);
}

.footer__scroll-top {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 70px;
  height: 70px;
  color: #ffffff;
  background: #000000;
  border-radius: 50%;
  transition: 0.7s;
}

.footer__scroll-top:hover {
  background: rgba(255, 255, 255, 0.1);
}

.footer__copyright {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: uppercase;
}

.footer__copyright-first-section {
  color: rgba(255, 255, 255, 0.6);
  font-weight: 200;
  font-size: 0.8rem;
  margin-bottom: 5px;
  order: 2;
}

.footer__copyright-second-section {
  color: #ffffff;
  font-weight: 400;
  font-size: 1rem;
  margin-bottom: 5px;
  order: 1;
}

.footer__copyright-second-section-link {
  color: #ffffff;
  text-decoration: none;
  transition: 0.7s;
}

.footer__copyright-second-section-link:hover {
  background: rgba(255, 255, 255, 0.3);
}

@media (max-width: 575px) {
  .footer__social-block {
    width: 100%;
    max-width: 200px;
  }
}

.preloader {
  height: 100vh;
  width: 100%;
  align-items: center;
  background: #000000;
  display: flex;
  justify-content: center;
  position: fixed;
  left: 0;
  top: 0;
  transition: opacity 0.3s linear;
  z-index: 9999;
}

.preloader-hide {
  display: none;
}

html,
body {
  height: 100%;
  font-family: 'Josefin Sans', sans-serif;
}

.body-fixed {
  position: fixed;
  overflow-y: hidden;
  transition: overflow-y 0.2s linear, position 0.2s linear 0.2s;
}
